import { FC } from "react"
import { Route, Routes } from "react-router-dom"
import Main from "pages/Main"
import Order from "pages/Order"

import "./styles.scss"

const App: FC = () => (
  <Routes>
    <Route
      path="/nfd"
      element={<Main />}
    />
    <Route
      path="/nfd/order/:id"
      element={<Order />}
    />
    <Route
      path="/nfd/order/ordered/:id"
      element={<Order />}
    />
  </Routes>
)

export default App
